const express = require("express");
const router = new express.Router();

import banksController from "../controllers/bank";
import terminalsController from "../controllers/terminal";

/* GET ALL TERMINALS */
router.get("/terminals", terminalsController.show);
router.get("/pos", terminalsController.show);


/* GET ALL BANKS */
router.get("/banks", banksController.show);
router.get("/banks/:id", banksController.showById);

export default router;
